import Vue from 'vue'
import Router from 'vue-router'

const System = () => import('../components/system/system.vue')
const Login = () => import('../components/login/login.vue')

const Home = () => import('../components/system/home/home.vue')
const PersonalInfo = () => import('../components/system/personalInfo/personalInfo.vue')

const Conference = () => import(/* webpackChunkName: "group-conference" */ '../components/system/conference/conference.vue')
const CheckVote = () => import(/* webpackChunkName: "group-conference" */ '../components/system/checkVote/checkVote.vue')
const Official = () => import(/* webpackChunkName: "group-conference" */ '../components/system/official/official.vue')
const Note = () => import(/* webpackChunkName: "group-conference" */ '../components/system/note/note.vue')
const AddRecord = () => import(/* webpackChunkName: "group-conference" */ '../components/system/addRecord/addRecord.vue')
const CompileOfficial = () => import(/* webpackChunkName: "group-conference" */ '../components/system/compileOfficial/compileOfficial.vue')
const AddOfficiaConference = () => import(/* webpackChunkName: "group-conference" */ '../components/system/addOfficiaConference/addOfficiaConference.vue')

const Inform = () => import(/* webpackChunkName: "group-inform" */ '../components/system/inform/inform.vue')
const SendInform = () => import(/* webpackChunkName: "group-inform" */ '../components/system/sendInform/sendInform.vue')

const AddLinkman = () => import(/* webpackChunkName: "group-staff" */ '../components/system/addLinkman/addLinkman.vue')

const Device = () => import(/* webpackChunkName: "group-device" */ '../components/system/device/device.vue')

const Topic = () => import(/* webpackChunkName: "group-topic" */ '../components/system/topic/topic.vue')
const CollectTopic = () => import(/* webpackChunkName: "group-topic" */ '../components/system/collectTopic/collectTopic.vue')
const AddConference = () => import(/* webpackChunkName: "group-topic" */ '../components/system/addConference/addConference.vue')
const TopicList = () => import(/* webpackChunkName: "group-topic" */ '../components/system/topicList/topicList.vue')
const AddTopic = () => import(/* webpackChunkName: "group-topic" */ '../components/system/addTopic/addTopic.vue')
const CheckMeeting = () => import(/* webpackChunkName: "group-topic" */ '../components/system/checkMeeting/checkMeeting.vue')

const Vote = () => import(/* webpackChunkName: "group-vote" */ '../components/system/vote/vote.vue')
	
