Vue.use(Router)

export default new Router({
  routes: [{
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/',
      name: 'System',
      component: System,
      meta: {
        requireAuth: true
      },      
      children: [{
          path: ':id/',
          name: 'ChildRouter',
          meta: {
            requireAuth: true
          },
          component: Home
        },
        {
          path: ':id/CollectTopic',
          name: 'CollectTopic',
          meta: {
            requireAuth: true
          },
          component: CollectTopic
        },
        {
          path: ':id/AddTopic',
          name: 'AddTopic',
          meta: {
            requireAuth: true
          },
          component: AddTopic
        },
        {
          path: ':id/Topic',
          name: 'Topic',
          meta: {
            requireAuth: true
          },
          component: Topic
        },
        {
          path: ':id/TopicList',
          name: 'TopicList',
          meta: {
            requireAuth: true
          },
          component: TopicList
        },
        {
          path: ':id/AddConference',
          name: 'AddConference',
          meta: {
            requireAuth: true
          },
          component: AddConference
        },
        {
          path: ':id/Inform',
          name: 'Inform',
          meta: {
            requireAuth: true
          },
          component: Inform
        },
        {
          path: ':id/Vote',
          name: 'Vote',
          meta: {
            requireAuth: true
          },
          component: Vote
        },
        {
          path: ':id/Conference',
          name: 'Conference',
          meta: {
            requireAuth: true
          },
          component: Conference
        },
        {
          path: ':id/AddLinkman',
          name: 'AddLinkman',
          meta: {
            requireAuth: true
          },
          component: AddLinkman
        },
        {
          path: ':id/AddRecord',
          name: 'AddRecord',
          meta: {
            requireAuth: true
          },
          component: AddRecord
        },
        {
          path: ':id/Device',
          name: 'Device',
          meta: {
            requireAuth: true
          },
          component: Device
        },
        {
          path: ':id/CheckMeeting',
          name: 'CheckMeeting',
          meta: {
            requireAuth: true
          },
          component: CheckMeeting
        },
        {
          path: ':id/Note',
          name: 'Note',
          meta: {
            requireAuth: true
          },
          component: Note
        },
        {
          path: ':id/SendInform',
          name: 'SendInform',
          meta: {
            requireAuth: true
          },
          component: SendInform
        },
        {
          path: ':id/Official',
          name: 'Official',
          meta: {
            requireAuth: true
          },
          component: Official
        },
        {
           path: ':id/CompileOfficial',
           name: 'CompileOfficial',
           meta: {
             requireAuth: true
           },
           component: CompileOfficial
        },
        {
          path: ':id/PersonalInfo',
          name: 'PersonalInfo',
          meta: {
            requireAuth: true
          },
          component: PersonalInfo
       	},
        {
          path: ':id/AddOfficiaConference',
          name: 'AddOfficiaConference',
          meta: {
            requireAuth: true
          },
          component: AddOfficiaConference
        },
        {
          path: ':id/CheckVote',
          name: 'CheckVote',
          meta: {
            requireAuth: true
          },
          component: CheckVote
        }      
      ]
    }
  ]
})



